import '../common/csp-inject';
import '../styles/global.css';

import { TeamsHostProvider } from '@addressbook/components-metaos-host';
import React from 'react';
import ReactDOM from 'react-dom';
// import { MobileApp } from '../mobile/MobileApp';
import { init } from '../common/init';
import { LogCategory, logger, ScenarioName, StepName } from '../logger';
import { ErrorBoundary } from '../views/containers/ErrorBoundary';

init(true);
ReactDOM.render(
    <ErrorBoundary>
        <TeamsHostProvider>{/* <MobileApp /> */}</TeamsHostProvider>
    </ErrorBoundary>,
    document.getElementById('root'),
    (): void => {
        logger.markScenario(ScenarioName.AppBoot, StepName.AppBootReactRendered);
        logger.logTraceInfo(LogCategory.AppBoot, 'React rendered mobile');
    }
);
